import React, { useEffect, useState } from "react";
import InformationLayout from "./InformationLayout";
import Spinner from '../../../Views/SpinnerWrapper/Spinner';
import SpinnerWrapper from '../../../Views/SpinnerWrapper/SpinnerWrapper'; 

const InformationLayoutContainer = ({breadcrumbs, components})=>{
    const [ contents, setContents ] = useState([]);
    const [ title, setTitle ] = useState('');
    useEffect(()=>{
        if(!!components.length){
            setContents(components[0].children)
            setTitle(components[0].title)
        }
    },[components])
    return (<>
        {
            components?
                <InformationLayout
                    breadcrumbs={breadcrumbs}
                    contents ={contents}
                    title={title}
                />
                : <SpinnerWrapper>
                    <Spinner size={100} />
                </SpinnerWrapper>
        }
        </>
    )
}

export default (InformationLayoutContainer);